* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html,
body,
#root,
#root > div {
  height: 100%;
  margin: 0;
  padding: 0;
}

#navbar {
    width: 100%;
    height: 100px;
    background-image: linear-gradient(to right, #1020ad , rgb(214, 7, 7));
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    position: fixed;
    top: 0;
    z-index: 1;
}

#navbarLeft {
    float: left;
}

#navbarMiddle {
    text-align: center;
}

#navbarRight {
    float: right;
}

#navbarLogo {
    height: 90px;
    margin-top: 5px;
    margin-bottom: 15px;
    margin-left: 25px;
    float: left;
}

.navItem {
    display: inline-block;
    width: 33%;
}


#guess {
    color: white;
    font-size: 14px;
    font-family: 'Ubuntu', sans-serif;
    margin-top: 40px;
}

.score {
    color: white;
    font-size: 15px;
    font-family: 'Ubuntu', sans-serif;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: right;
    margin-right: 25px;
    margin-top: 10px;
}

@media (max-width: 500px) {
    #navbarLogo {
        height: 50px;
        margin-left: 5px;
    }

    .score {
        font-size: 12px;
        margin-right: 5px;
    }
  }
#header {
    margin-top: 10px;
    width: 100%;
    height: 300px;
    background-image: linear-gradient(to right, #1020ad , rgb(214, 7, 7));
}

#instructions {
    color: white;
    font-family: 'Ubuntu', sans-serif;
    padding-top: 150px;
    font-size: 25px;
}

.headerSection {
    width: 33%;
    display: inline-block;
}

#headerLeft {
    float: left;
}

#headerMiddle {
    text-align: center;
}

#headerRight {
    float: right;
}

#warriors {
    height: 150px;
    float: left;
    margin-top: 100px;
    margin-left: 5%;
}

#nbalogo2 {
    height: 150px;
    float: right;
    margin-top: 100px;
    margin-right: 5%;
}

@media (max-width: 500px) {
    #nbalogo {
        height: 100px;
    }

    #nbalogo2 {
        height: 100px;
    }

    #instructions {
        font-size: 18px;
    }
  }
  
.container {
  background-image: linear-gradient(to right, #1020ad , rgb(214, 7, 7));
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 0px;
  width: 100%;
  margin: 0 auto;
}


.click-item {
  background-size: cover;
  background-position: center;
  height: 170px;
  width: 170px;
  margin: 8px 25px;
  border: 5px solid rgb(241, 241, 241);
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
}

.click-item:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: 0.1s;
}

.characters {
  height: 200px;
  border-radius: 5px;
  opacity: 0.8;

  transition: all 1s;
}

.characters:hover {
  opacity: 1;
}

.characterBox {
  padding: 20px;
  margin: 20px;
  border: solid grey 1px;
  border-radius: 5px;
  background-color: #dddddd;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

  transition: all 1s;
}

.characterBox:hover {
  background-color: #aaaaaa;
}

#footer {
    bottom: 0;
    width: 100%;
    height: 100px;
    background-image: linear-gradient(to right, #1020ad , rgb(214, 7, 7));
}

a {
    color: white;
    font-family: 'Ubuntu', sans-serif;
    transition: all 1s;
}

a:hover {
    text-decoration: none;
    color: #e480d8;
}

#logo {
    height: 40px;
}

#name {
    font-size: 18px;
}

.footerWrapper {
    margin-top: 20px;   
}
